.subscription {
  padding-top: 119px;
  padding-bottom: 120px;
  background-color: $ghost-white;

  @include vp-1023 {
    padding-top: 97px;
    padding-bottom: 95px;

    h2 {
      font-size: 42px;
      line-height: 42px;
      padding-bottom: 32px;
    }
  }

  @include vp-767 {
    padding-top: 70px;
    padding-bottom: 70px;

    h2 {
      font-size: 32px;
      line-height: 32px;
      padding-bottom: 22px;
    }
  }

  h2 {
    font-family: $ff-special;
    color: $color-sapphire;
    font-size: 49px;
    font-weight: 700;
    letter-spacing: 0.03em;
    line-height: 49px;
    text-align: center;
    margin: 0;
    padding: 0;
    padding-bottom: 35px;
  }
}

.subscription__container {
  display: grid;
  grid-template-columns: 1fr;
}

.subscription .subscription__tabs {
  margin-bottom: 39px;

  @include vp-1023 {
    margin-bottom: 43px;
  }

  @include vp-767 {
    margin-bottom: 35px;
  }
}
